<template>
  <div class="content container">
    <transition name="fade" appear>
      <div class="content__top" v-if="this.$store.state.orderInfo">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link :to="{ name: 'cart' }" class="breadcrumbs__link">
              Корзина
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> Оформление заказа </a>
          </li>
        </ul>

        <h1 class="content__title">
          Заказ оформлен <span>№ {{ this.$store.state.orderInfo.id }}</span>
        </h1>
      </div>
    </transition>

    <transition name="fade" appear>
      <section class="cart" v-if="this.$store.state.orderInfo">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту
              придет письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся
              с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
            </p>

            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key"> Получатель </span>
                <span class="dictionary__value">
                  {{ this.$store.state.orderInfo.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Адрес доставки </span>
                <span class="dictionary__value">
                  {{ this.$store.state.orderInfo.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Телефон </span>
                <span class="dictionary__value">
                  {{ this.$store.state.orderInfo.phone }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Email </span>
                <span class="dictionary__value">
                  {{ this.$store.state.orderInfo.email }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Способ оплаты </span>
                <span class="dictionary__value">
                  {{ this.$store.state.orderInfo.paymentType }}
                </span>
              </li>
            </ul>
          </div>

          <div class="cart__block">
            <ul class="cart__orders">
              <li
                class="cart__order"
                v-for="item in this.$store.state.orderInfo.basket.items"
                :item="item"
                :key="item.product.id"
              >
                <h3>{{ item.product.title }}</h3>
                <b>{{ numberFormat(item.product.price) }} ₽</b>
                <span>Артикул: {{ item.product.id }}</span>
              </li>
            </ul>

            <div class="cart__total">
              <p>
                Доставка:
                <b>
                  {{
                    formatDeliveryPrice(
                      Number(this.$store.state.orderInfo.deliveryType.price)
                    )
                  }}
                </b>
              </p>
              <p>
                Итого:
                <b>{{ this.$store.state.orderInfo.basket.items.length }}</b>
                товара на сумму
                <b
                  >{{
                    numberFormat(this.$store.state.orderInfo.totalPrice)
                  }}
                  ₽</b
                >
              </p>
            </div>
          </div>
        </form>
      </section>
    </transition>
  </div>
</template>

<script>
import numberFormat from "@/helpers/numberFormat.js";
export default {
  methods: {
    numberFormat,
    formatDeliveryPrice(price) {
      if (price > 0) {
        return numberFormat(price) + " ₽";
      } else {
        return "бесплатно";
      }
    },
  },
  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo.id === this.$route.params.id
    ) {
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  },
};
</script>

<style scoped>
.container {
  min-height: calc(100vh - 408px);
}
.fade-enter-active {
  transition: opacity 1s ease;
}
.fade-enter-from {
  opacity: 0;
}
</style>
